          html: `
          <shn-statistic v-model="unit.value" :unit="''"/>
          `,
          javascript: `
          export default {
            data() {
              return {
                value:'800547.20'
              }
            }
          }
          `
        },
        value: '800547.20'
      },
      //DEMO
      fontSize: {
