          description: '',
          dataTypes: 'Number',
          optional: '',
          default: '24'
        },
        {
          parameter: 'decimalFontSize',
          description: '',
          dataTypes: 'Number',
          optional: '',
          default: '16'
        },
        {
          parameter: 'unitFontSize',
          description: '',
          dataTypes: 'Number',
          optional: '',
          default: '16'
        },
        {
          parameter: 'unit',
          description: '',
          dataTypes: 'String',
          optional: '',
          default: ''
        },
        {
          parameter: 'groupSeparator',
          description: '',
          dataTypes: 'Boolean',
          optional: 'true / false',
          default: 'true'
        },
        {
          parameter: 'decimalNum',
          description: '',
          dataTypes: 'Number',
          optional: '',
          default: '2'
        },
        {
          parameter: 'intFontClass',
          description: '',
          dataTypes: 'String',
          optional: '',
          default: ''
        },
        {
          parameter: 'decimalFontClass',
          description: '',
          dataTypes: 'String',
          optional: '',
          default: ''
        },
        {
          parameter: 'unitFontClass',
          description: '',
          dataTypes: 'String',
          optional: '',
          default: ''
        },
        {
          parameter: 'percent',
          description: '',
          dataTypes: 'Boolean',
          optional: 'true / false',
          default: 'false'
        }
      ]
    }
  },
  methods: {}
}
</script>
